@media screen and (max-width: 575.5px) {
  .foot_util_link_div {
    text-align: left !important;
    padding-left: 30px !important; } }

.footer_wrapper {
  display: block;
  min-height: 100px;
  padding: 0; }

.wrap_util_links {
  display: block;
  min-height: 100px;
  padding: 0; }

.foot_util_link_div {
  display: block;
  min-height: 40px;
  padding-right: 15px;
  text-align: center;
  margin-top: 40px;
  font-weight: bold;
  font-size: 15px;
  padding-top: 5px;
  font-family: 'Lexend Deca', sans-serif;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
  opacity: 1; }

.foot_util_link_div span:hover {
  opacity: 0.9;
  text-decoration: underline;
  cursor: pointer; }

.wrap_social_icons {
  display: block;
  position: relative;
  border-top: 1px solid #A9A9A9;
  min-height: 70px;
  margin-top: 20px;
  margin-bottom: 10px;
  background-color: #FFFFFF; }

.footer_copyrights_footer {
  margin: 0;
  font-size: 14px;
  padding: 20px 0 30px 60px; }

.social_icons_icons {
  display: block;
  min-height: 40px; }

.social_icons_icons_wrapper {
  position: absolute;
  min-height: 30px;
  width: 350px;
  right: 30px;
  display: inline-block; }

.footer_followus {
  min-width: 90px !important;
  font-family: 'Lexend Deca', sans-serif;
  font-size: 15px;
  border: none !important;
  padding-top: 7px;
  cursor: text !important; }

.social_icons_icons_wrapper span {
  display: inline-block;
  width: 35px;
  height: 35px;
  margin-right: 10px;
  border: 1px solid #A8A8A8;
  border-radius: 40px;
  cursor: pointer;
  opacity: 0.8; }

.social_icons_icons_wrapper .soc_icon:hover {
  opacity: 1; }

.social_icons_icons_wrapper span:nth-child(2) {
  background-image: url("../images/social_footer/facebook.png");
  background-size: 80% 80%;
  background-position: center center;
  background-repeat: no-repeat;
  -webkit-transition: .5s;
  -o-transition: .5s;
  transition: .5s; }

.social_icons_icons_wrapper span:nth-child(3) {
  background-image: url("../images/social_footer/twitter.png");
  background-size: 80% 80%;
  background-position: center center;
  background-repeat: no-repeat;
  -webkit-transition: .7s;
  -o-transition: .7s;
  transition: .7s; }

.social_icons_icons_wrapper span:nth-child(4) {
  background-image: url("../images/social_footer/instagram.png");
  background-size: 80% 80%;
  background-position: center center;
  background-repeat: no-repeat;
  -webkit-transition: .9s;
  -o-transition: .9s;
  transition: .9s; }

.social_icons_icons_wrapper span:nth-child(5) {
  background-image: url("../images/social_footer/youtube.png");
  background-size: 80% 80%;
  background-position: center center;
  background-repeat: no-repeat;
  -webkit-transition: 1.1s;
  -o-transition: 1.1s;
  transition: 1.1s; }
