.connected_first h2, .connected_second h2, .acc_third h2 {
  color: #fff;
  font-size: 40px;
  font-weight: bold;
  border-left: solid #fff;
  padding-left: 10px;
  padding-right: 10px;
  margin: 10px 0 10px 20px; }

@media screen and (max-width: 500px) {
  .connected_f_img {
    height: 200px !important; } }

@media screen and (max-width: 729.5px) {
  .connected_second {
    background-image: url("../images/connected/connected_sec_sm.jpg") !important;
    min-height: 1400px !important;
    background-position: 50% 100% !important; }
  .connected_second span {
    margin: 50px 0 20px 30px !important;
    font-size: 20px !important; }
  .acc_third_img {
    width: 450px !important;
    height: 250px !important; } }

@media screen and (min-width: 730px) and (max-width: 1110px) {
  .connected_second {
    background-position: 50% 50% !important; } }

